import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { ShippingAddressService } from 'src/app/service/shipping-address.service';

@Component({
  selector: 'app-shipping-address-list',
  templateUrl: './shipping-address-list.component.html'
})
export class ShippingAddressListComponent implements OnInit {

  PageTitle = "Manage Shipping Address";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private shippingaddressService: ShippingAddressService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(

      new TextField({
        label: "Client",
        key: "client.label",
      }),
      new TextField({
        label: "City",
        key: "city",
      }),
      new TextField({
        label: "State",
        key: "state",
      }),
      new TextField({
        label: "Pincode",
        key: "pin_code",
      }),
      new TextField({
        label: "Country",
        key: "country_name",
      }),
      new TextField({
        label: "Is Default",
        key: "is_default",
      }),
    );
    let canEdit = true;
    let canDelete = true;

    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/shipping-address/edit/',
        id: 'shipping_address_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'shipping-address/',
        id: 'shipping_address_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.shippingaddressService.getShippingAddressList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




