import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/service/common.service';
import { ShippingAddressService } from 'src/app/service/shipping-address.service';

@Component({
  selector: 'app-shipping-address-add',
  templateUrl: './shipping-address-add.component.html'
})
export class ShippingAddressAddComponent implements OnInit {

  PageTitle = "Shipping Address"

  loadingState = false;
  addForm: FormGroup;

  showLoader = false;
  formErrors = {
    state: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;
  countryListArray: any = [];
  clientListArray: any = [];

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private shippingaddressService: ShippingAddressService,
    private commonservice: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      city: [null, Validators.compose([Validators.required])],
      state: [null, Validators.compose([Validators.required])],
      street_address: [null],
      country_name: [''],
      country_id: [null, Validators.compose([Validators.required])],
      pin_code: [null, Validators.compose([Validators.required])],
      is_default: [false, Validators.compose([Validators.required])],
      client_id: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
    this.getCountryList();
    this.getMasterData();
    if (this.isEditing) {
      this.getEditObject()
    }
  }

  getCountryList() {
    this.commonservice.getCountryList().subscribe((res: any) => {
      if (res.success) {
        this.countryListArray = res.data.country_list;
      }
    });
  }

  getMasterData() {
    this.shippingaddressService.getMasterData().subscribe((res: any) => {
      if (res.success) {
        this.clientListArray = res.data.client;
      }
    });
  }




  getEditObject() {
    this.shippingaddressService.getShippingAddressById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
      } else {
        this.router.navigateByUrl('/manage/shipping-address');
      }
    });
  }

  getCountryName(data) {
    this.addForm.patchValue({ country_name: data.label });
  }



  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('client_id', formData.value.client_id);
      data.append('city', formData.value.city);
      data.append('state', formData.value.state);
      data.append('country_name', formData.value.country_name);
      data.append('country_id', formData.value.country_id);
      data.append('pin_code', formData.value.pin_code);
      data.append('street_address', formData.value.street_address);
      data.append('is_default', formData.value.is_default);
      data.append('order_type_code', formData.value.order_type_code);

      if (this.isEditing) {
        this.shippingaddressService.editShippingAddress(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/shipping-address');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('state')) {
                this.formErrors['state'] = obj['state'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.shippingaddressService.addShippingAddress(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/shipping-address');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('state')) {
                this.formErrors['state'] = obj['state'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
}
