import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { ShippingAddressListComponent } from './shipping-address-list/shipping-address-list.component';
import { ShippingAddressAddComponent } from './shipping-address-add/shipping-address-add.component';
import { ShippingAddressService } from 'src/app/service/shipping-address.service';
import { NgSelectModule } from '@ng-select/ng-select';


const routes: Routes = [
  {
    path: "",
    component: ShippingAddressListComponent,
    data: { title: 'shipping_address_list' }
  },
  {
    path: "add",
    component: ShippingAddressAddComponent,
    data: { title: 'add_shipping_address' }
  },
  {
    path: 'edit/:id',
    component: ShippingAddressAddComponent,
    data: { title: 'edit_shipping_address' }
  },

]

@NgModule({
  declarations: [ShippingAddressListComponent, ShippingAddressAddComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    PaginationModule,
    SharedModule,
    NgSelectModule
  ], providers: [ShippingAddressService]
})
export class ShippingAddressModule { }

